/* roboto-condensed-300 - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: local("Roboto Condensed Light"),
    local("RobotoCondensed-Light"),
    url("../fonts/roboto-condensed-v18-regular.woff") format("woff");
}

/* roboto-condensed-300italic - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: italic;
  font-weight: 300;
  src: local("Roboto Condensed Light Italic"),
    local("RobotoCondensed-LightItalic"),
    url("../fonts/roboto-condensed-v18-latin-300italic.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v18-latin-300italic.woff") format("woff");
}

/* roboto-condensed-regular - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Condensed"),
    local("RobotoCondensed-Regular"),
    url("../fonts/roboto-condensed-v18-regular.woff") format("woff");
}

/* roboto-condensed-italic - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: italic;
  font-weight: 400;
  src: local("Roboto Condensed Italic"),
    local("RobotoCondensed-Italic"),
    url("../fonts/roboto-condensed-v18-latin-italic.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v18-latin-italic.woff") format("woff");
}

/* roboto-condensed-700italic - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: italic;
  font-weight: 700;
  src: local("Roboto Condensed Bold Italic"),
    local("RobotoCondensed-BoldItalic"),
    url("../fonts/roboto-condensed-v18-bold-italick.woff") format("woff");
}

/* roboto-condensed-700 - latin */

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Condensed Bold"),
    local("RobotoCondensed-Bold"),
    url("../fonts/roboto-condensed-v18-bold.woff") format("woff");
}

/* pt-mono-regular - latin_cyrillic-ext_cyrillic */

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: local("PT Mono"),
    local("PTMono-Regular"),
    url("../fonts/pt-mono-v7-latin-regular.woff2") format("woff2"),
    url("../fonts/pt-mono-v7-latin-regular.woff") format("woff");
}
